.GameController {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
}
  
.SizePicker {
  background: white;
  height: 2rem;
  width: 3rem;
  border:1px solid #ccc;
  vertical-align: top;
}

.NewGame {
  background: rgb(248, 86, 57);
  color: white;
  font-size: 1rem;
  border: 0;
  font-weight: 600;
  height: 2rem;
  margin-left: 1rem;
}

p {
  display: inline;
  color: gray;
  font-size: 1rem;
  margin-right: 0.5rem;
}