body {
  margin: 0;
  padding: 0;
  background-color: rgb(231, 231, 231);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
}

.GameContainer {
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Header {
  background: black;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 4rem;
  width: 100%;
}
  
.placeholder {
  width: 15vw;
}

.GameController {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
}
  
.SizePicker {
  background: white;
  height: 2rem;
  width: 3rem;
  border:1px solid #ccc;
  vertical-align: top;
}

.NewGame {
  background: rgb(248, 86, 57);
  color: white;
  font-size: 1rem;
  border: 0;
  font-weight: 600;
  height: 2rem;
  margin-left: 1rem;
}

p {
  display: inline;
  color: gray;
  font-size: 1rem;
  margin-right: 0.5rem;
}
.CardBack {
  display: flex;
  justify-content: center;
  align-items: center;
}

.CardBack img {
  width: 80%;
  height: auto;
}

.hidden {
  opacity: 0.1;
  pointer-events: none;
}

.Game {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

.scoreContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  height: 2rem;
  font-size: 1rem;
  border: 2px solid black;
  border-radius: 5%;
  font-weight: 800;
  background: rgb(231, 231, 231);
}

.scoreContainer p {
  font-size: 1.5rem;
}
